<template>
  <b-container class="pt-3 pb-4 bg">
    <div class="green-border p-2 ml-1 mr-1">
      <h2>Укажите размеры в миллиметрах</h2>
      <b-row class="mt-3">
        <b-col style="text-align:end">
          <label for="width">
            Ширина
            <span>W</span>
          </label>
        </b-col>
        <b-col>
          <b-form-input v-model="width" id="width" size="sm" placeholder="Введите ширину..."></b-form-input>
        </b-col>
      </b-row>

      <b-row class="mt-2">
        <b-col style="text-align:end">
          <label for="height">
            Толщина
            <span>H</span>
          </label>
        </b-col>
        <b-col>
          <b-form-input v-model="height" id="height" size="sm" placeholder="Введите толщину..."></b-form-input>
        </b-col>
      </b-row>

      <b-row class="mt-2">
        <b-col style="text-align:end">
          <label for="length">
            Длинна
            <span>L</span>
          </label>
        </b-col>
        <b-col>
          <b-form-input v-model="length" id="length" size="sm" placeholder="Введите длинну..."></b-form-input>
        </b-col>
      </b-row>

      <h2 class="mt-3">Выберите исходные данные для расчета</h2>

      <b-form-group class="mt-2 mb-0 text-white">
        <b-row class="mt-2">
          <b-col style="text-align:end">
            <b-form-radio v-model="selected" value="N">
              Количество в штуках
              <span>N</span>
            </b-form-radio>
          </b-col>
          <b-col>
            <b-form-input v-model="countN" size="sm" placeholder="Введите кол-во в штуках..."></b-form-input>
          </b-col>
        </b-row>

        <b-row class="mt-2">
          <b-col style="text-align:end">
            <b-form-radio v-model="selected" value="E">
              Количество в м
              <sup>3</sup>
              <span>E</span>
            </b-form-radio>
          </b-col>
          <b-col>
            <b-form-input v-model="countE" size="sm" placeholder="Введите кол-во в куб. м..."></b-form-input>
          </b-col>
        </b-row>
      </b-form-group>

      <b-row class="mt-2">
        <b-col style="text-align:end">
          <label for="cost">
            Стоимость 1 м
            <sup>3</sup>
          </label>
        </b-col>
        <b-col>
          <b-form-input v-model="cost" id="cost" size="sm" placeholder="Введите стоимость..."></b-form-input>
        </b-col>
      </b-row>

      <div class="text-center">
        <b-button variant="success" class="mt-4" @click="calc" :disabled="isNotValid">Рассчитать</b-button>
      </div>
    </div>

    <b-row class="green-border p-2 mt-4 ml-1 mr-1 justify-content-center">
      <b-card class="result">
        <b-card-text>
          Объем пиломатериалов:
          <span>{{ allV }}</span> м
          <sup>3</sup>
        </b-card-text>
        <b-card-text>
          1 доска или брус:
          <span>{{ oneV }}</span> м
          <sup>3</sup>
        </b-card-text>
        <b-card-text>
          В 1 м
          <sup>3</sup>:
          <span>{{ allThing }}</span> штук доски или бруса
          такого размера
        </b-card-text>
        <b-card-text>
          Стоимость пиломатериалов:
          <span>{{ resultAllCost }}</span>
        </b-card-text>
        <b-card-text>
          Стоимость пиломатериалов 1 доски или бруса:
          <span>{{ resultOneCost }}</span>
        </b-card-text>
      </b-card>
    </b-row>
  </b-container>
</template>

<script>
export default {
  name: "WoodCalc",

  data() {
    return {
      selected: "N",
      width: null,
      height: null,
      length: null,
      countN: null,
      countE: null,
      cost: null,
      allV: null,
      oneV: null,
      resultAllCost: null,
      resultOneCost: null,
      allThing: null,
    };
  },

  methods: {
    calc() {
      this.oneV = +(
        (this.width * this.height * this.length) /
        1000000000
      ).toFixed(3);
      this.allV =
        this.selected === "N"
          ? +(this.countN * this.oneV).toFixed(2)
          : +this.countE;
      this.allThing = +(this.allV / this.oneV).toFixed(2);
      this.resultAllCost = this.allV * this.cost;
      this.resultOneCost = (this.resultAllCost * this.oneV) / this.allV;
    },
  },

  computed: {
    isNotValid() {
      if (
        !!this.width &&
        +this.width !== 0 &&
        !!this.height &&
        +this.height !== 0 &&
        !!this.length &&
        +this.length !== 0 &&
        ((!!this.countN && +this.countN !== 0) ||
          (!!this.countE && +this.countE !== 0)) &&
        !!this.cost &&
        +this.cost !== 0
      ) {
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>

